var React = require('react/addons');
var cx = require('../../bem-cx');
var Tab = require('./tab');


var Tabs = React.createClass({displayName: 'Tabs',

  getInitialState: function() {
    return {
      activeTab: 0
    };
  },

  activateTab: function(index) {
    var tabs = this.props.children;

    if (!Array.isArray(tabs)) {
      tabs = [tabs];
    } 

    var tab = tabs[index];
    this.setState({activeTab: index});
    if (tab.props.onActivate) {
      tab.props.onActivate();
    }
  },

  render: function() {
    var tabs = this.props.children;

    if (!Array.isArray(tabs)) {
      tabs = [tabs];
    }    
    
    var tabButtons = [];
    var tabBodies = [];
    var i;
    var tab;
    var active;
    var buttonClasses;
    var states;
    var panelClasses;

    for (i=0; i<tabs.length; i++) {
      tab = tabs[i];
      active = i === this.state.activeTab;

      buttonClasses = {
        'tabs__button': true,
      };

      panelClasses = {
        'tabs__panel': true
      };

      states = {
        'active': active
      };

      tabButtons.push(React.createElement("li", {key: tab.props.name, className: cx(buttonClasses, {states:states}), onClick: this.activateTab.bind(this, i)}, tab.props.name));
      tabBodies.push(React.createElement("li", {key: tab.props.name, className: cx(panelClasses, {states:states})}, tab.props.children));
    }

    return (
      React.createElement("div", {className: "mediacat-tabs"}, 
        React.createElement("div", {className: "mediacat-tabs__buttons-wrapper"}, 
          React.createElement("ul", {className: "mediacat-tabs__buttons"}, tabButtons)
        ), 
        React.createElement("ul", {className: "mediacat-tabs__panels"}, tabBodies)
      )
    );
  }
});

export default Tabs;


